
const DB_NAME = 'TodoList'
const DB_VERSION = 1
let DB

export default {
	async getDb() {
		return new Promise((resolve, reject) => {
			if(DB) { return resolve(DB) }
			let request = window.indexedDB.open(DB_NAME, DB_VERSION)
			
			request.onerror = e => {
				console.log('打開資料庫出現錯誤', e)
				reject('Error')
			}
	
			request.onsuccess = e => {
				DB = e.target.result
                console.log('打開資料庫成功')
				resolve(DB)
			}
			
			request.onupgradeneeded = e => {
				console.log('資料庫版本更新')
				let db = e.target.result
                //db.createObjectStore("todoList", { autoIncrement: true, keyPath:'id' })
                var temp = db.createObjectStore("todoList", { autoIncrement: true, keyPath:'id' })
                temp.createIndex("NameColumns","name",{ unique:false })
			}
		})
	},
    async deleteDb(){
        console.log('deleteDb')
        let db = await this.getDb()
        db.close()
        var DBDeleteReq = window.indexedDB.deleteDatabase(DB_NAME)
        DBDeleteReq.onsuccess = function(event) { 
            console.log("資料庫刪除成功",event)
            DB = null
        } 
        DBDeleteReq.onerror = e => {
            console.log("資料庫刪除出現錯誤",e)
        }         
        DBDeleteReq.onblocked = function () {
            console.log("無法刪除資料庫")
        }
    },
    async getValue(type = null) {
		let db = await this.getDb()
		return new Promise((resolve, reject) => {
			let trans = db.transaction(["todoList"],'readonly')
            trans.oncomplete = function (){
                
            }
            let store = trans.objectStore("todoList")
            let dbRequest = store.getAll()
            dbRequest.onsuccess = (event) => { // 成功後的回撥
                var list = event.target.result
                if(type !== null){
                    list = list.filter(item=>{ return item.type == type})
                }
                resolve(list) // 返回物件
            }
            trans.onerror = (event) => {
                reject(event)
            }
		})
	},
    async saveValue(data) {
		let db = await this.getDb()
		return new Promise((resolve, reject) => {
			let trans = db.transaction(["todoList"],'readwrite')
            let store = trans.objectStore("todoList")
            store.add(data)  
			trans.oncomplete = () => {   
				resolve(true)
			}
            trans.onerror = (event) => {
                console.log('event',event)
                reject(event)
            }
		})
	},
    async updateValue(data) {
		let db = await this.getDb()
		return new Promise((resolve, reject) => {
			let trans = db.transaction(["todoList"],'readwrite')
            trans
                .objectStore("todoList")
                .get(data.id)
                .onsuccess = (event)=>{
                    const newObject = { ...event.target.result, ...data }
                    trans
                        .objectStore("todoList") 
                        .put(newObject) 
                        .onsuccess = (event) => {
                            resolve(event.target.result)
                        }
                }
                trans.onerror = (event) => {
                    reject(event)
                }
		})
	},
    async deleteData(id){
        let db = await this.getDb()
		return new Promise((resolve, reject) => {
			let trans = db.transaction(["todoList"],'readwrite')
            let store = trans.objectStore("todoList")
            store.delete(id)  
			trans.oncomplete = () => {   
				resolve(true)
			}
            trans.onerror = (event) => {
                console.log('event',event)
                reject(event)
            }
		})
    }
}